@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
header {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(209, 37, 37);
  text-align: center;
  font-family: "Rock Salt", cursive;
  width: 100%;
  height: 600px;
  background-image: url(/wbs-cookblog/static/media/coco2.3ec19492.jpg);
  background-size: cover;
}

header h1 {
  font-size: 100px;
  text-shadow: 4px 4px 4px rgb(0, 0, 0);
}

.link {
  color: rgb(209, 37, 37);
  font-size: 30px;
  text-decoration: none;
  font-weight: bold;
  border: 2px solid rgb(209, 37, 37);
  padding: 7px 10px;
  margin: 5px 5px;
  border-radius: 3px;
}
.home{
  display:flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
}

.container{
  width: 50%;
  display:flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid rgb(209, 37, 37);
  box-shadow: 5px 5px 5px rgb(204, 66, 66);
  padding:2em;
  margin:2em;
}
.home .container h2{
  font-family: "Rock Salt", cursive;
  padding: 1em;
  text-align: center;
}

.home .container p{
  font-size: 1.5em;
  padding:1rem 0;
  text-align: justify;
}

nav {
  text-align: end;
  padding: 2em;
}

nav img{
  float:left;
  height: 50px;
}

label {
  text-align: center;
  margin:1px;
}
#social-buttons {
    width: auto;
    height: 60px;
    display: flex;
    align-items: center;
    background-color: lightgray;
}

.categories{
  display: flex;
  flex-wrap: wrap;
}

.heart{
  color:red ;
}
.favorites-bar{
  background-image: url(/wbs-cookblog/static/media/coco2.3ec19492.jpg);
  font-family: "Rock Salt", cursive;
  
}

.favorites-bar:link{
  text-decoration: none;
}


.active{
  color:blue
}

#social-buttons{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}


.flex-container{
  display:flex;
  flex-direction: column;
  justify-content: center;
}

#recipeGrid, a:-webkit-any-link {
  
  text-decoration: none !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media only screen and (max-width: 780px) {
#four {
  display: flex;
  flex-direction: column;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
}
h4, h5, h6, p, #recipeText {
    font-family: 'Roboto', sans-serif !important;
    color: #282c34 !important;
    font-size: 1,5em !important;
}

h1, h2, span.MuiTypography-root {
    font-family: "Rock Salt", cursive !important;
    color: rgb(209, 37, 37) !important;
}

a.link {
    font-family: 'Roboto', sans-serif !important;
    
}

#collection, .categories {
    display: flex;
    flex-wrap: wrap;
    justify-items: center;
    align-items: center;
    justify-content: center;
    align-content: center;
}

#card {
    width: 30%;
    text-align: center;
}

#collection, #card {
    background-color: lightgray;
}

#recipe {
    border: 2px solid rgb(209, 37, 37);
}

#recipeGrid {
    background-color:lightgray;
}

#recipeCard {
    background-color: white;
    border: 2px solid rgb(209, 37, 37);
    width: 90%;
    color: #282c34;
    margin: 5% 5%;
    
}

#pic {
    width: 40%;
    margin: 1%;
}


@media only screen and (max-width: 600px) {
    #collection {
      display: block;
    }
    #card {
        width: 90%;
    }
    #recipeCard {
        width: 90%;
        margin: 5%;
    }
    #pic {
        width: 25%;
        margin: 1%;
    }
  }

